<template>
   <div id="echarts">
        <div class="big">
            <div @click="alt('one')" id="one">
                <Geo :geo='Data1' @chart='getCharts'/>
            </div>
        </div>
        <div class="small">
            <div @click="alt('two')" id="two">
                <Pie :pie='Data2' @chart='getCharts'/>
            </div>
            <div @click="alt('three')" id="three">
                <Water :water='Data3' @chart='getCharts'/>
            </div>
            <div @click="alt('four')" id="four">
                <Axis :axis='Data4' @chart='getCharts'/>
            </div>
        </div>
   </div>
</template>
<script>
import data from '../../config/data.js'
import Geo from './Geo.vue'
import Pie from './Pie.vue'
import Water from './Water.vue'
import Axis from './Axis.vue'
export default {
    data(){
        return {
            Data1: {},
            Data2: {},
            Data3: {},
            Data4: {},
            charts: []
        }
    },
    components: {
        Geo,
        Pie,
        Water,
        Axis
    },
    created(){
        let obj = this.copyObj(data.obj), objOne = this.convert(data.obj), objTwo = this.convert(obj.sort(this.compare).slice(0, 6))
        let name = [], val = []
        for(let i=0;i<data.obj.length;i++){
            name.push('')
            val.push(data.obj[i].value)
        }
        let inData = [
                {value:335, name:'直达'},
                {value:679, name:'营销广告'},
                {value:1548, name:'搜索引擎'}
            ],
            outData = [
                {value:335, name:'直达'},
                {value:310, name:'邮件营销'},
                {value:234, name:'联盟广告'},
                {value:135, name:'视频广告'},
                {value:1048, name:'百度'},
                {value:251, name:'谷歌'},
                {value:147, name:'必应'},
                {value:102, name:'其他'}
            ]
        this.Data1 = {
            node: '#one',
            objOne: objOne,
            objTwo: objTwo
        }
        this.Data2 = {
            node: '#two',
            inData: inData,
            outData: outData
        }
        this.Data3 = {
            node: '#three',
            name: '东东么么哒',
            data: [0.6, 0.5, 0.4, 0.3]
        }
        this.Data4 = {
            node: '#four',
            name: name,
            val: val
        }
    },
    mounted(){
        const charts = this.charts
        window.onresize = function(){
            for(let i=0;i<charts.length;i++){
                charts[i].resize()
            }
        }
    },
    methods: {
        convert: function(arr){
            let res = []
            for(let i=0;i<arr.length;i++){
                let geoCoord = data.geo[arr[i].name]
                if(geoCoord){
                    res.push({
                        name: arr[i].name,
                        value: geoCoord.concat(arr[i].value)
                    })
                }
            }
            return res
        },
        copyObj: function(obj){
            return JSON.parse(JSON.stringify(obj))
        },
        compare: function (a, b) {
            return b.value - a.value;
        },
        getCharts: function(data){
            this.charts.push(data)
        },
        alt: function(str){
            // alert(str)
        }
    }
}
</script>
<style lang="less" scoped>
#echarts{
    position: absolute;
    left: 0;
    top:0;
    width: 100%;
    height: 100%;
    background-color: #eee;
    .big{
        width: 60%;
        height: 100%;
        float: left;
        #one{
            width: 100%;
            height: 100%;
        }
    }
    .small{
        width: 40%;
        height: 100%;
        float: left;
        #two, #three, #four{
            width: 100%;
            height: 33.33%;
        }
    }
    @media screen and (max-width: 1050px){
        .big{
            width: 100%;
            height: 60%;
            #one{
                width: 100%;
                height: 100%;
            }
        }
        .small{
            width: 100%;
            height: 40%;
            #two, #three, #four{
                width: 100%;
                height: 100%;
            }
        }
    }
}
</style>


